@import url(https://fonts.googleapis.com/css?family=Open+Sans);
*{
  margin: 0;
  padding: 0;
}
html{
  font-family: sans-serif;
  color: #323648;
  overflow-y: scroll;
  overflow-x: hidden;
}
body{
  background-color: #fafafa;
}


div.loading-user-card{
    margin-top: 40px;
    grid-row: 3/4;
    grid-column: 1 / 4;
}

.user-profile{
    grid-row: 3/4;
    grid-column: 1 / 4
}
div.user-profile-grid{
    margin: 52px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: repeat(4, auto);
    grid-column-gap: 3rem;
    grid-row-gap: 10px;
    align-items: center;
}
div.user-bio{
    grid-row: 3/4;
}
div.user-bio p {
    text-align: left;
    font-size: 16px;
    font-family: unset;
}
div.user-img{
    grid-row: 1/4;
    grid-column: 1 / 2;
}
div.profile-user-settings {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    display: flex;
    max-width: 500px;
}
h1.user-name{
    flex: 1 1;  
    text-align: left;
    
}
a.btn-visit{
    flex: 2 1;
    /* width: 50px; */
}
div.user-stats{
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    
}
div.user-stats ul{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    
}
div.user-stats ul li{
    margin-bottom: 10px;
    margin-right: 35px;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    position: relative;
	box-sizing: border-box;
	transition: all 200ms ease;
    position: relative;
    padding: 2px;

    /* transition: all ease-in-out .2s; */
}
div.user-stats ul li:hover {
	background: rgba(0,0,0,0);
    color: #3a7999;
    border-bottom: 3px solid #3a7999
	/* box-shadow: inset 0 0 0 3px #3a7999; */
}
h1{
    line-height: 37.05px;
    font-family: "Open Sans", Arial, sans-serif;
    color: #262626;
    font-weight: 300;
}
.btn-visit {
    display: block;
    /* margin: 0 auto 50px; */
    background: #fff;
    border: 1px solid #9e9e9e;
    color: #707070;
    padding: 10px 40px;
    overflow: hidden;
    max-width: 100px;
    color: #262626; 
    text-decoration: none; 
    margin-top: 2px;
    margin-left: 15px;
    align-self: center
}
.profile-stats{
    grid-row: 1 /2 ;
    grid-column: 2 / 3;
}
.user-stats ul {
    display: flex;
    text-align: center;
    padding: 1.2rem 0;
    border-top: 0.1rem solid #dadada;
    border-bottom: 0.1rem solid #dadada;
}

img.user-img{
    /* margin-left: 20px;
    margin-bottom: 1em; */
    width: 152px;
    height: 152px;
    border-radius: 50%;
    /* max-width: 50%; */
}
a.btn-visit:hover{
    transition: background 0.5s ease;
    background-color: #3a7999;
    color: #fff;
}

/* smarphone design responsive design */
@media only screen and (max-width: 650px) {

    .user-profile {
        grid-row: 3/4;
        grid-column: 2 / 3;
    }
    div.user-img {
        grid-row: 1/2;
        grid-column: 1 / 2;
    }
    img.user-img {
        width: 102px;
        height: 102px;
    }
    h1.user-name {
        text-align: left;
        font-size: 25px;
        display: inline-block;
        font-weight: 300;
        line-height: unset;
        line-height: 26px;
    }
    .user-stats ul{
        justify-content: space-around;
    }
    div.user-bio{
        grid-column: 1 / 4;
    }
    div.profile-user-settings{
        grid-row: 1 / 2;
        grid-column: 2 / 3;
        display: grid;
        grid-template-columns: 1f 1fr;
        grid-auto-rows: 1fr 1fr;
    }
    div.profile-user-settings h3{
        grid-column: 1 / 2
    }
    .btn-visit{
        grid-column: 1 / 2;
    
        padding: 4px;
        width: 100px;
        margin-left: 0;
        margin-top: 15px;
    }
}
@media only screen and (max-width: 450px) {
    div.user-profile-grid{
        margin: 52px 20px 20px 20px;
    }
    div.data-wrapper ul li h3 {
        font-size: 16px;
    
    }

}



.lds-ring {
    display: inline-block;
    /* position: absolute; */
    width: 64px;
    height: 64px;
    top: 35%;
    left: 50%;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 6px solid #00688B;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #00688B transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
div.user-data-loading{
    grid-row: 4/5;
    grid-column: 2 / 3;

    position: relative;
    margin: 30px 0 0 80px;
    width: 14px;
    height: 14px;

}
div.user-data-grid{
    grid-row: 4 / 5;
    grid-column: 2 / 3;
}
div.data-wrapper ul{
  list-style: none;
  margin-top: 40px;
  text-align: left;
  
}
div.data-wrapper ul li{
    padding: 5px;
    z-index: -2;
    box-sizing: border-box;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
    margin: 20px;
    display: flex;
    align-items: center;
    margin-left: 0;
    /* justify-content: space-between; */
}
div.data-wrapper ul li.repos-list{
    justify-content: space-between;
}
.user-data-img{
    width: 55px;
    height: 55px;
    border-radius: 50%;
    
}
h3 .user-data-login{
    margin-left: 12px;
    color: black;
    text-decoration: none;
}

div.user-repos-stars {
    margin-left: 15px;
    border: 1px solid black;
    padding: 5px;
}
div.data-wrapper ul li h3{
    flex: 1 1;
    align-self: flex-start;
}
a.user-repos-name{
    text-decoration: none;
    color: #323648;
} 
/* smarphone design responsive design */
@media only screen and (max-width: 650px) {

    div.user-data-loading{
        grid-row: 4/5;
        grid-column: 1 / 3;
    }
    div.user-stats {
        grid-row: 2 / 3;
        grid-column: 1 / 4;
    }
    div.user-data-grid {
        grid-row: 4 / 5;
        grid-column: 1 / 4;
    }

}
.App{
  text-align: center;
  display: grid;
  grid-template-rows: 70px auto;
  grid-template-columns: minmax(50px,1fr) 1fr minmax(50px,1fr);
  grid-row-gap: 2em;
}
#header{
  grid-column: 1/5;
  grid-row: 1/2;
  background-color: #800080;
}
#header h1{
  text-align: left;
  margin-top: 20px;
  margin-left: 10px;
  color: #FFF;
}
div.wrapper{
  grid-column: 2/3;
  grid-row: 2/3;
}
#search-bar{
  width: 100%;
  position: relative
}
#search-bar input{
  margin-top: 20px;
  width: 258px;
  border: 3px solid #800080;
  padding: 5px;
  height: 20px;
  border-radius: 5px;
  outline: none;
  color: #9DBFAF;
}
#search-bar input:focus{
  color: #800080;
}
.searchButton{
  margin-left: -9px;
  margin-top: 20px;
  position: absolute;  
  /* right: -50px; */
  width: 40px;
  height: 36px;
  border: 1px solid #800080;
  background: #800080;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 10px;
}
.searchButton i{
  font-size: 20px;
}


@media only screen and (max-width: 650px) {
  .App {
    overflow: hidden;
    text-align: center;
    display: grid;
    grid-template-rows: 70px auto;
    grid-template-columns: 2px 1fr 2px;
    grid-row-gap: 2em;
  }
  div.wrapper {
    margin-right: 10%;
}
  #header h1{
    width: auto;
    grid-column: 1/4;
  }
  div#search-bar {
   /* width: 80%; */
  }
}
